<template>
  <div class="button-container">
    <div class="button-box"
         role="button"
         tabindex="0"
         :class="{active:$parent.$data.currentSection === to}"
         @click="clickHandler">
      {{text}}
    </div>
  </div>
</template>

<script>
export default {
  name: "NavButton",props:{text:String,to:Number},
  methods:{
    clickHandler(){
      this.$parent.$data.currentSection = this.$props.to;
    }
  }
}
</script>

