<template>
  <div class="box">
    <div class="title">{{title}}</div>
    <div class="element">
      <input type="radio" value="yok" :id="'yok'+name" v-model="value">
      <label :for="'yok'+name">Yok</label>
    </div>
    <div class="element">
      <input type="radio" value="var" :id="'var'+name" v-model="value">
      <label :for="'var'+name">Var</label>
    </div>
    <div v-if="value === 'var'" >
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  name: "RadioForm",
  props:{
   model:String,title:String,positiveResponseModel:String,changeHandler:{type:Function,default:()=>{}}
  },
  data(){
    return{
      name:this.$uuid(),
      value:"",
      positiveResponse:'',
    }
  },watch:{
    value(){
      this.$props.changeHandler(this.value,this.$props.model);
      // this.$parent.$data[this.$props.model] = this.value;
    }
    ,positiveResponse(){
      // this.$parent.$data[this.$props.positiveResponseModel] = this.positiveResponse;
    }
  }
}
</script>

