<template>
  <div class="y-input">
    <input :type="type" :id="inputId" v-model="value">
    <label :for="inputId" :class="{filled:hasValue}">{{placeholder}}</label>
    <div class="example" v-if="example">Ör : {{example}}</div>
  </div>
</template>

<script>
export default {
  name: "YInput",
  props:{
    placeholder:String,type:String,example:String,
    model:{type:String,default:""},
    changeHandler:{type:Function,default:()=>{}},
  },data(){
    return{
      inputId:this.$uuid(),
      value:"",
      hasValue:this.$props.type !== "text",
    }
  },watch:{
    value(){
      this.hasValue = this.value !== "";
      this.$props.changeHandler(this.value,this.$props.model);
      // this.$parent.$data[this.$props.model] = this.value;
    }
  }
}
</script>

<style scoped>
  .y-input{
    display: grid;
    grid-template-columns: 1fr;
  }
  .example{
    color: #888;
    font-size: 0.8rem;
    margin-top: 5px;
  }
  input{
    position: relative;
    border:none;
    border-bottom: 2px solid #018dd4;
    height: 40px;
    font-size: 1.3rem;
    cursor: text;
    padding-right: 10px;
    padding-top: 15px;
    outline: none;
    z-index: 1;
    background-color: rgba(0,0,0,0);
  }
  input:focus{
    border-bottom: 2px solid #b1003c;
  }
  .filled,input:focus ~ label,input:hover ~ label{
    font-size: 1rem;
    padding-left: 2px;
    padding-top: 0;
  }
  label{
    position: absolute;
    font-size: 1.5rem;
    padding-right: 10px;
    //padding-left: 10px;
    padding-top: 10px;
    height: 40px;
    color:#555;
    transition: 0.2s;
    z-index: 0;
  }
</style>