<template>
  <div class="box">
    <div class="title">{{title}}</div>
    <div v-for="(val,index) in valueArray" :key="index" class="element">
      <input :type="checkBox?'checkbox':'radio'" :value="val" :id="index+name" v-model="value">
      <label :for="index+name">{{val}}</label>
    </div>
  </div>
</template>

<script>
export default {
name: "RadioForm",
  props:{
    valueArray:{type:Array},model:String,title:String,changeHandler:{type:Function,default:()=>{}},checkBox:{type:Boolean,default:false}
  },
  data(){
  return{
    name:this.$uuid(),
    value:this.$props.checkBox?[]:'',

  }
  },watch:{
    value(){
      this.$props.changeHandler(this.value,this.$props.model);
      // this.$parent.$data[this.$props.model] = this.value;
    }
  }
}
</script>

<style scoped>
.box{
  display: flex;
  border: 0.01rem solid #ff0058;
  padding: 10px;
  border-radius: 10px;
}
